<template>
  <div class="content">
    <div class="head">
      <img src="../../../static/image/icon_back.png" alt @click="back" />
      我的订单
      <div class="head_top">
        <p :class="{active: tab == 1}" @click="tabs(1)">待收货</p>
        <p :class="{active: tab == 2}" @click="tabs(2)">已发货</p>
        <p :class="{active: tab == 3}" @click="tabs(3)">已收货</p>
      </div>
    </div>

    <div class="bscroll" ref="bscroll">
      <div class="bscroll-container">
        <div class="main" v-for="item in list">
          <div class="main_title">
            <p>{{item.goodslist.goods_title}}</p>
            <p v-if="item.status == 1">待发货</p>
            <p v-if="item.status == 2">已发货</p>
            <p v-if="item.status == 3">已收货</p>
          </div>
          <div class="main_goods" @click="detail(item.id)">
            <div class="goods_left">
              <img :src="item.goodslist.goods_logo" alt />
            </div>
            <div class="goods_right">
              <div>{{item.goodslist.goods_title}}</div>
              <div class="right_price">
                <div class="price1">￥{{item.goodslist.market_price*1}}</div>
                <div class="num">X{{item.number}}</div>
              </div>
            </div>
          </div>
          <div class="cancle">
            <p class="cancle_top">
              共{{item.ordernum}}件 总计：
              <span>￥{{item.goodslist.market_price*item.number}}</span>
            </p>
            <div class="cancle_boo" v-if="item.status == 2">
              <p @click="show(item.order_no)">查看物流</p>
              <p @click="affirmShou(item.id)">确认收货</p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="shade" v-if="modal" @click="hide">
      <div class="alert" @click.stop>
        <div class="alert-content">{{express}}</div>
        <div class="alert-button" @click="copy">复制</div>
      </div>
    </div>
    <div class="foot"></div>
  </div>
</template>
<script type="text/ecmascript-6">
import Bscroll from "better-scroll";
export default {
  name: "myorder",
  data() {
    return {
      num: 2,
      dropDown: false,
      tab: 1,
      modal: false,
      express: "",
      list: []
    };
  },
  methods: {
    scrollFn() {
      this.$nextTick(() => {
        if (!this.scroll) {
          this.scroll = new Bscroll(this.$refs.bscroll, {
            click: true,
            scrollY: true,
            probeType: 3
          });
        } else {
          this.scroll.refresh();
        }
        this.scroll.on("touchEnd", pos => {
          //touchEnd（手指离开以后触发）
          if (this.scroll.maxScrollY > pos.y + 15) {
            let status = this.tab;
            status = status;
            this.$post("/apiv1/order/orderType", {
              status: status,
              pageNo: this.num
            }).then(res => {
              this.num++;
              this.list = this.list.concat(res.data);
              if (res.data.length == 0) {
                this.num--; //无数据时保持pageNo的值
                this.$msg("暂无更多");
              }
            });
            this.scroll.refresh();
          }
        });
      });
    },
    tabs(e) {
      this.tab = e;
      if (this.tab == 2) {
        //切换时重置num值和list数据
        this.num = 2;
        this.list = [];
        this.getData(e);
      } else {
        this.num = 2;
        this.list = [];
        this.getData(e);
      }
    },
    copy() {
      let that = this;
      that.$copyText(this.express).then(
        function(e) {
          that.modal = false;
          that.$toast({
            message: "复制成功",
            duration: 3000
          });
        },
        function(e) {
          that.$toast({
            message: "复制失败",
            duration: 3000
          });
        }
      );
    },
    back() {
      this.$router.go(-1);
    },
    show(express) {
      this.modal = true;
      this.express = express;
    },
    hide() {
      this.modal = false;
    },
    // detail(id) {
    //   this.$router.push("/orderdetails?id=" + id);
    // },
    affirmShou(id) {
      this.$post("/apiv1/order/affirmShou", {
        id: id
      }).then(res => {
        this.$msg(res.msg);
        if (res.code == 1) {
          this.tab = 3;
          this.getData(3);
        }
      });
    },
    getData(e) {
      let status = e;
      this.$post("/apiv1/order/orderType", {
        status: status
      }).then(res => {
        let that = this;
        console.log(res);
        if (res.code == 1) {
          this.list = res.data;
          if (this.list < 1) {
            that.$msg(res.msg);
          }
        } else {
          that.$msg(res.msg);
        }
      });
    }
  },
  mounted() {
    this.getData(1);
    this.scrollFn();
  }
};
</script>
<style lang="scss" scoped>
@import "../../assets/scss/mixin";

.bscroll {
  height: calc(100vh - 7rem);
}

.content {
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background: #0d1637;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;

  .shade {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.5);
    z-index: 1000;

    .alert {
      position: relative;
      z-index: 999;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      width: 80%;
      background: white;
      height: 300px / $ppr;
      position: relative;
      border-radius: 15px / $ppr;

      .alert-content {
        display: flex;
        flex: 1;
        padding: 30px / $ppr;
        align-items: center;
        justify-content: center;
      }

      .alert-button {
        width: 100%;
        display: flex;
        height: 80px / $ppr;
        color: #0ba5fb;
        font-size: 28px / $ppr;
        align-items: center;
        justify-content: center;
        border-top: 2px / $ppr solid #e0e0e0;
      }
    }
  }

  .head {
    width: 100%;
    height: 250px / $ppr;
    position: relative;
    text-align: center;
    line-height: 110px / $ppr;
    font-size: 36px / $ppr;
    color: #fff;
    background: #0d1637;
    z-index: 100;
    padding-top: 30px / $ppr;

    img {
      width: 17px / $ppr;
      height: 29px / $ppr;
      display: block;
      position: absolute;
      top: 70px / $ppr;
      left: 44px / $ppr;
    }
  }

  .head_top {
    display: flex;
    justify-content: space-around;

    p.active {
      border-bottom: 3px / $ppr solid white;
    }
  }

  .main {
    width: 100%;
    font-family: PingFang-SC-Medium;
    font-size: 24px / $ppr;
    font-weight: normal;
    font-stretch: normal;
    line-height: 100px;
    letter-spacing: -1px;
    background-color: #1b285a;
    margin-bottom: 0.3rem;
    color: #fff;

    .order-item {
      display: flex;
      flex-direction: column;
    }

    .main_title {
      width: 750px / $ppr;
      height: 85px / $ppr;
      display: flex;
      justify-content: space-between;
      align-items: center;
      border-bottom: 1px / $ppr solid #97a6fc;

      p:first-child {
        padding-left: 30px / $ppr;
        color: #fff;
      }

      p:last-child {
        padding-right: 30px / $ppr;
        color: #0ba5fb;
      }
    }

    .main_goods {
      height: 246px / $ppr;
      display: flex;
      justify-content: space-around;
      border-bottom: 1px / $ppr solid #97a6fc;

      .goods_left {
        width: 30%;

        img {
          width: 200px / $ppr;
          height: 200px / $ppr;
          margin: 30px / $ppr 0 0 20px / $ppr;
        }
      }

      .goods_right {
        width: 497px / $ppr;
        height: 65px / $ppr;
        font-family: PingFang-SC-Regular;
        font-size: 26px / $ppr;
        font-weight: bold;
        font-stretch: normal;
        line-height: 40px / $ppr;
        letter-spacing: 0px;
        color: #fff;
        margin-top: 47px / $ppr;

        .right_price {
          display: flex;
          justify-content: space-between;
          margin-top: 40px / $ppr;

          .price1 {
            font-family: PingFang-SC-Regular;
            font-size: 28px / $ppr;
            font-weight: bold;
            letter-spacing: 0px;
            color: #4776f8;

            span {
              font-family: PingFang-SC-Regular;
              font-size: 24px / $ppr;
              font-weight: normal;
              letter-spacing: 0px;
              color: #979797;
              text-decoration: line-through;
            }
          }

          .num {
            padding-right: 100px / $ppr;
          }
        }
      }
    }

    .cancle {
      width: 100%;
      font-family: PingFang-SC-Medium;
      font-size: 26px / $ppr;
      font-weight: normal;
      font-stretch: normal;
      line-height: 100px / $ppr;
      letter-spacing: -1px / $ppr;
      color: #b9b9b9;
      padding-bottom: 0.7rem;

      .cancle_top {
        color: #b9b9b9;
        margin-left: 430px / $ppr;

        span {
          color: #fff;
          padding-left: 20px / $ppr;
        }
      }

      .cancle_boo {
        display: flex;
        justify-content: space-between;

        p:first-child {
          width: 180px / $ppr;
          height: 54px / $ppr;
          background-color: #97a6fc;
          border-radius: 5px / $ppr;
          border: solid 1px / $ppr #1b285a;
          color: #1b285a;
          text-align: center;
          line-height: 54px / $ppr;
          margin-left: 300px / $ppr;
        }

        p:last-child {
          width: 180px / $ppr;
          height: 54px / $ppr;
          background-color: #97a6fc;
          border-radius: 5px / $ppr;
          border: solid 1px / $ppr #1b285a;
          color: #1b285a;
          text-align: center;
          line-height: 54px / $ppr;
          margin-right: 30px / $ppr;
        }
      }
    }
  }

  .boo {
    margin-top: 30px / $ppr;
    width: 750px / $ppr;
    height: 20px / $ppr;
    background-color: #f8fbff;
  }
}
.foot{
  width: 100%;
  height: 100px/ $ppr;
  background-color: #f8fbff;
}
</style>



